import { io } from "socket.io-client";

import data from "./data_mock";

export const connectToDojot = async (
  url,
  sensor,
  machine,
  onSensorData,
  onMachineData
) => {
  const authURL = `${url}auth`;
  const authToken = await getAuthToken(authURL);

  console.log('getSocketIOToken');
  const socketIOURL = `${url}stream/socketio`;
  const socketIOToken = await getSocketIOToken(socketIOURL, authToken.jwt);

  console.log('io.connect');
  const client = io.connect(socketIOURL, {
    query: `token=${socketIOToken.token}`,
    transports: ["polling"],
  });

  console.log('client.on');
  client.on("all", (message) => {
    console.log(message);
    if (message.metadata) {
      switch (message.metadata.deviceid) {
        case sensor:
          console.log(`Message from ${sensor} (sensor)`);
          onSensorData(message);
          break;

        case machine:
          console.log(`Message from ${machine} (machine)`);
          onMachineData(message);
          break;

        default:
          console.log(`Message from ${message.metadata.deviceid} (unknown)`);
          break;
      }
    }
  });

  let i = 0;
  let date = data[i]["Data"];

  // Every 2 seconds, sends messages to sensor
  setInterval(() => {
    const currentData = data
      .filter((d) => d["Data"] === date)
      .map(Object.values);
    currentData.forEach((d) => {
      console.log(`Sent message to ${sensor}`);
      console.log(d);
      client.emit(sensor, JSON.stringify(d));
    });

    // Finds next date
    while (++i < data.length) {
      if (data[i]["Data"] !== date) {
        date = data[i]["Data"];
        break;
      }
    }
    // After all data has been sent,
    // resets counter and starts all over again
    if (i === data.length) {
      i = 0;
      date = data[i]["Data"];
    }
  }, 2000);
};

export const getAuthToken = async (url) => {
  const authResponse = await fetch(url, {
    method: "post",
    headers: new Headers({ "content-type": "application/json" }),
    body: new Blob([JSON.stringify({username:"admin", passwd:"admin"})], {type: "application/json"}),
  });
  const authJson = await authResponse.json();
  return authJson;
};

export const getSocketIOToken = async (url, jwtToken) => {
  const tokenResponse = await fetch(url, {
    method: "get",
    credentials: "include",
    headers: new Headers({ Authorization: `Bearer ${jwtToken}` }),
  });
  const tokenJson = await tokenResponse.json();
  return tokenJson;
};
