import { message } from "antd";

import actionTypes from "./actionTypes";

import { connectToDojot } from "../services/dojot";

export const setConfig = (url, sensor, machine) => {
  connectToDojot(url, sensor, machine, onSensorData, onMachineData)
    .then(() => {
      console.log(`Conectado a: ${url}`);
    })
    .catch((e) => {
      message.error(`Não foi possível conectar-se a: ${url}`);
    });

  return {
    type: actionTypes.SET_CONFIG,
    url: url,
    sensor: sensor,
    machine: machine,
  };
};

const onSensorData = (data) => {};

const onMachineData = (data) => {};
