// ACTION TYPES
import actionTypes from './actionTypes';

// INITIAL STATE
const initialState = {
  url: null,
  sensor: null,
  machine: null,
};

/**
 * reducer
 *
 * @param state
 * @param action
 */
const reducer = (state = initialState, action = undefined) => {
  switch (action.type) {
    case actionTypes.SET_CONFIG:
      return {
        ...state,
        url: action.url,
        sensor: action.sensor,
        machine: action.machine,
      };

    // DEFAULT
    default:
      return state;
  }
};

// EXPORT
export default reducer;
